.about {
  display: flex;
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: $main-color;

  // @media screen and (min-width: 768px) {
  //   display: flex;
  // }
}

.about__container {
  @media screen and (min-width: 768px) {
    display: flex;
  }
}

// .about__wrap-img{
//   display: flex;
//   align-items: center;
// }

.swiper {
  @media screen and (min-width: 1280px) {
    width: 600px;
    height: 500px;
  }
}

.about__image {
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    width: 350px;
  }

  @media screen and (min-width: 1280px) {
    width: 600px;
  }
}

// text

.about__wrap-text {
  // @media screen and (min-width: 768px) {
  //   max-width: 350px;
  // }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    margin-left: 30px;
  }

  @media screen and (min-width: 1280px) {
    margin-left: 80px;
  }
}

.about__text-title {
  font-weight: 500;
  font-size: 20px;
  line-height: 1.16;
  letter-spacing: 0.03em;
  color: $main-text-color;
  margin-bottom: 15px;
  text-align: center;
}

// .about__item::before{
//   content: "";
//   width: 20px;
//   height: 20px;
//   border-radius: 50%;
//   background-color: $hover-color;
// }

.about__item {
  display: flex;
  align-items: center;
  &:not(:last-child) {
    margin-bottom: 10px;
  }
}

.about__icon {
  fill: $hover-color;
}

.about__item-text {
  margin-left: 5px;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.03em;
  color: $second-text-color;

  // &:not(:last-child) {
  //   margin-bottom: 10px;
  // }
}
